<template>
  <div class="history" id="goods_history">
    <back-header title="我的足迹">
      <span class="management" @click="toggleEdit">管理</span>
    </back-header>
    <scroll ref="scroll" :class="{edit: edit}" @scrollToEnd="loadMore" clickable :is-finished="finished">
      <div class="goods" @click="goGoods(index, item.goods.id)" v-for="(item, index) in list" :key="item.id">
        <div @click.stop class="check-wrap">
          <van-checkbox @change="computedAll" icon-size="0.36rem" checked-color="#E1234D" v-if="edit" class="check" v-model="item.checked" />
        </div>
        <div class="img" :style="{backgroundImage: `url(${item.cover})`}" />
        <div class="info">
          <p class="van-multi-ellipsis--l3">{{ item.goods.title }}</p>
          <div class="price">
            <p>¥{{ item.goods.price }}</p>
            <span>{{ item.visits_time }}</span>
          </div>
        </div>
      </div>
      <div v-if="false" style="height: 1.41rem;"></div>
    </scroll>
    <div class="footer" v-if="edit">
      <div class="all" @click="checkedAll=!checkedAll">
        <van-checkbox @change="toggleAll" v-model="checkedAll" icon-size="0.36rem" checked-color="#E1234D" />
        <span>全选</span>
      </div>
      <div class="del" @click="delHistory">删除</div>
    </div>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import cloneDeep from '../../common/js/clone-deep'
import { post } from '../../common/js/http'

const PAGE_SIZE = 10

export default {
  name: 'history',
  components: {
    BackHeader,
    Scroll
  },
  data() {
    return {
      list: [],
      edit: false,
      checkedAll: false,
      itemChange: false,
      parentChange: false,
      finished: true,
      page: 1
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    loadMore() {
      if (this.loading) return
      this.page++
      this.loading = true
      this.getList(false)
    },
    toggleEdit() {
      this.edit = !this.edit
      this.$nextTick(() => {
        setTimeout(() => {
          this.$refs.scroll.refresh()
        }, 20)
      })
    },
    computedAll() {
      if (this.parentChange) return
      this.itemChange = true
      this.checkedAll = !this.list.some(item => !item.checked)
      this.$nextTick(() => {
        this.itemChange = false
      })
    },
    toggleAll() {
      if (this.itemChange) return
      this.parentChange = true
      const copy = cloneDeep(this.list)
      copy.forEach(item => {
        item.checked = this.checkedAll
      })
      this.list = copy
      this.$nextTick(() => {
        this.parentChange = false
      })
    },
    getList(fullLoading = true) {
      let loading = null
      if (this.page === 1 && fullLoading) {
        loading = this.$skeLoading('#goods_history')
      }
      post('/shop/user/getUserVisits', {
        page: this.page,
        size: PAGE_SIZE
      })
        .then(({ data: { data } }) => {
          this.finished = data.length < PAGE_SIZE
          data.forEach(item => {
            item.cover = item.goods.thumb_urls.split(',')[0]
            item.checked = false
          })
          this.list = this.page === 1 ? data : this.list.concat(data)
          this.loading = false
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
          loading && loading.close()
        })
        .catch(() => {
          loading && loading.close()
        })
    },
    goGoods(index, id) {
      if (this.edit) {
        this.$set(this.list, index, {
          ...this.list[index],
          checked: !this.list[index].checked
        })
      } else {
        this.$router.push(`/goods/${id}`)
      }
    },
    delHistory() {
      const loading = this.$toast.loading()
      const ids = []
      this.list.map(item => {
        if (item.checked) {
          ids.push(item.id)
        }
      })
      if (!ids.length) {
        this.$toast.fail('请选择商品')
        return
      }
      post('/shop/user/delUserVisits', {
        visitsids: ids.join(',')
      })
        .then(() => {
          for (let i = 0; i < this.list.length; i++) {
            if (this.list[i].checked) {
              this.list.splice(i, 1)
              i--
            }
          }
          loading.close()
        })
        .catch(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
    }
  }
}
</script>

